.advantages {
    width: 100%;
    height: 100%;
    background-color: $color-white-light;
    &__body {
        display: flex;
        flex-wrap: wrap;
        padding: 60px 0 60px 113px;
    }

    &__card {
        position: relative;
        max-width: 50%;
        display: flex;
        padding: 25px 130px 25px 14px;
        flex-direction: column;
    }

    &__img {
       height: 56px;
       position: absolute;
       top: 42px;
       left: -70px;
    }

    &__title {
        font-size: $font-size-h3;
        font-weight: $font-weight-h1-h2-h3;
        padding-bottom: 15px;
    }

    &__subtitle {
        font-size: 18px;
        line-height: 140%;
    }
}


@media (max-width: 844px) {
    .advantages {

		&__card {
            padding-right: 80px;
		}

		&__title {
            font-size: 22px;
		}

		&__subtitle {
            font-size: 14px;
		}

    }

}

@media (max-width: 656px) {
    .advantages {

		&__body {
            flex-direction: column;
		}

		&__card {
            max-width: 100%;
		}
    }

}

@media (max-width: 411px) {
    .advantages {
        
        &__body {
            padding: 60px 0 60px 80px;

        }

        &__card {
            padding-right: 20px;
        }
    }
}