.structure-lesson {

    background-color: #F7F8F7;
    &__container {
        max-width: 1140px;
        margin: 0 auto;

        display: flex;
        flex-direction: row;

        @media (max-width: 999px) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__left-part {
        width: 50%;
        padding: 0 20px;
        @media (max-width: 999px) {
            width: 100%;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 45px;
        line-height: 55px;
        padding-top: 60px;
        @media (max-width: 999px) {
            font-size: 40px;
            line-height: normal;
            padding-top: 40px;
        }
        @media (max-width: 768px) {
            font-size: 35px;
            padding-top: 20px;
        }
        @media (max-width: 480px) {
            font-size: 30px;
        }
        @media (max-width: 360px) {
            font-size: 25px;
        }
    }

    &__under-title {
        font-weight: 600;
        font-size: 25px;
        line-height: 30px;
        padding-top: 40px;
        @media (max-width: 999px) {
            line-height: normal;
            padding-top: 20px;
        }
        @media (max-width: 768px) {
            font-size: 20px;
        }
        @media (max-width: 480px) {
            font-size: 18px;
        }
        @media (max-width: 360px) {
            font-size: 16px;
        }
    }

    &__choose-data {
        display: flex;
    }

    &__data-number {
        padding-top: 20px;

        font-weight: 600;
        font-size: 200px;
        line-height: 244px;
        color: #005C3D;
        @media (max-width: 999px) {
            font-size: 170px;
            line-height: normal;
        }
        @media (max-width: 768px) {
            font-size: 140px;
        }
        @media (max-width: 480px) {
            font-size: 110px;
        }
        @media (max-width: 360px) {
            font-size: 80px;
        }
    }

    &__data-number-text {
        padding: 72px 0 0 19px;
        max-width: 346px;
    }

    &__choose-data-heading {
        font-weight: 700;
        font-size: 25px;
        line-height: 30px;
    }

    &__choose-data-text {

        padding-top: 20px;
        font-weight: 400;
        font-size: 20px;
        line-height: 130%;

        @media (max-width: 999px) {
            font-size: 18px;
            line-height: normal;
        }
        @media (max-width: 768px) {
            font-size: 16px;
        }
        @media (max-width: 480px) {
            font-size: 15px;
        }
    }

    &__arrow-right {
        width: 60px;
        height: 26px;
        cursor: pointer;
        float: right;
    }

    &__right-part {
        width: 50%;
        padding: 88px 0 50px 0;
        background-color: #F7F8F7;
        @media (max-width: 999px) {
            width: 100%;
            padding: 0 0 50px 0;
        }
    }

    &__slider-line {

        margin-top: 85px;
        width: 100%;
        height: 1px;
        background-color: #0E110D;

        display: flex;
        flex-direction: row;
        align-items: center;
        @media (max-width: 999px) {
            margin-top: 35px;
        }
    }

    &__slider-line-snake {
        width: 33%;
        height: 3px;
        background-color: #005C3D;
        position: relative;
    }

    &__container-for-slide-bar {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        width: 560px;


        overflow: hidden;
        @media (max-width: 1127px) {
            width: 500px;
        }
        @media (max-width: 999px) {
            margin: 0 auto;
        }
        @media (max-width: 635px) {
            width: 500px;
        }
        @media (max-width: 505px) {
            width: 400px;
        }
        @media (max-width: 405px) {
            width: 300px;
        }
    }

    &__snake-slide-bar {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;

        position: relative;
        right: 20px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__slider-img {
        // padding-right: 10px;

    }

    &__slider-img {
        padding-top: 108px;
        @media (max-width: 999px) {
            padding-top: 35px;
        }
    }
}

.animation-img {
    
    &__1 {
        right: 200%;
        transition: 0.5s;
        // @media (max-width: 999px) {
        //     right: 120%;
        //     transform: translateX(-50%);
        // }
    }

    &__2 {
        right: 100%;
        transition: 0.5s;
        // @media (max-width: 999px) {
        //     right: 70%;
        //     transform: translateX(-50%);
        // }
    }

    &__3 {
        right: 0;
        transition: 0.5s;
        // @media (max-width: 999px) {
        //     right: 50%;
        //     transform: translateX(-50%);
        // }
    }
}

.animation-slider {
    &__1 {
        transform: translateX(0);
        transition: 0.5s;
    }

    &__2 {
        transform: translateX(100%);
       transition: 0.5s; 
    }

    &__3 {
        transform: translateX(200%);
        transition: 0.5s;
    }
}

._button-green-mod {
    color: white;
    border-radius: 5px;
    transition: all .8s ease;
    background-color: #009C68;
    &:hover{
        background-color: #005C3D;
        cursor: pointer;
    }
}

.rates {

    max-height: 435px;
    background-color: $color-black-dark;
    color: $color-white;

    &__container {
        max-width: 1140px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
    }

    &__left-part {
        width: 50%;

    }

    &__heading {
        font-weight: 700;
        font-size: 45px;
        line-height: 55px;
        padding-top: 40px;
    }

    &__text-first {
        max-width: 343px;
        max-height: 75px;
        margin-top: 40px;

        font-weight: 400;
        font-size: 18px;
        line-height: 140%;

    }

    &__text-second {
        max-width: 343px;
        max-height: 75px;
        margin-top: 17px;

        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
    }

    &__btn {

        width: 197px;
        height: 48px;
        margin-bottom: 48px;

        background: $color-green-light;
        border-radius: 5px;

        margin-top: 27px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;

        font-weight: 500;
        font-size: 15px;
        line-height: 18px;

    }

    &__right-part {
    }

    &__happy-girl {
        margin-top: 52px;
        padding-right: 53px;
    }
}

@media (max-width: 1163px) {
    
    .rates {

		&__container {
            padding: 0 15px;
		}

		&__left-part {
            width: fit-content;
		}

		&__right-part {
            flex-grow: 1;
		}

		&__happy-girl {
            padding-right: 0;
            margin: 52px 0 0 auto;
            display: block;
		}
    }

}

@media (max-width: 938px) {
    
    .rates {

		&__container {
            padding: 0 15px;
		}

        &__heading {
            font-size: 35px;
        }

        &__text-first {
            font-size: 15px;
        }

        &__text-second {
            font-size: 15px;
        }

		&__left-part {
            width: fit-content;
		}

		&__right-part {
            flex-grow: 1;
		}

		&__happy-girl {
            padding-right: 0;
            margin-top: 0;
            display: block;
            // width: 444px;
            // height: 224px;
            height: 100%;
            width: 100%;
            object-fit: contain;
		}
    }

}

@media (max-width: 768px) {

    .rates {

		&__left-part {
            flex-shrink: 0;
		}

		&__heading {
            padding-top: 10px;
		}
    }


}

@media (max-width: 645px) {
    .rates {

        max-height: fit-content;

		&__container {
		}

		&__left-part {
            width: 100%;
		}

		&__heading {
		}

		&__text-first {
            margin-right: 0;
            width: 100%;
            max-width: none;
            text-align: justify;
            font-size: 22px;
            max-height: fit-content;
		}

		&__text-second {
            margin-right: 0;
            width: 100%;
            max-width: none;
            text-align: justify;
            font-size: 22px;
            max-height: fit-content;
		}

		&__btn {
            margin-bottom: 20px;
            width: 100%;
		}

		&__right-part {
            display: none;
		}

		&__happy-girl {
		}
    }

}